input {
    margin: 5px 4px;
    border: unset;
    border-bottom-style: ridge;
    border-bottom-width: 0.2rem;
    padding-left: 0.3rem;
}

input:focus {
    outline: none;
}

a {
    text-decoration: none;
}

body {
    margin: 0 auto;
    padding: 0;
}

.link {
    color: unset;
}

.logo {
    width: 63px;
}

li {
    list-style: none;
}

