#first-name,
#last-name {
    color: #7db08c;
    margin: 0;
    font-weight: bold;
    justify-items: flex-start;
}

#peter {
    display: inline;
    transition: transform 1s;
}

#peter:hover {
    cursor: pointer;
    text-shadow: 1px 1px 1px #61C2A2;
}

.img-circle {
    background: radial-gradient(rgba(147, 233, 190, 0.1) 0%, rgba(34, 167, 101, 0.5) 100%), url("../assets/p - 360.jpg");
    border-top: 1px solid rgb(115, 109, 234);
    border-bottom: 1px solid rgb(115, 109, 234);
    border-left: 6px solid rgb(115, 109, 234);
    border-right: 6px solid rgb(115, 109, 234);
    background-position: center;
    border-radius: 100%;
    box-shadow: -9px 2px 33px #61C2A2;
    width: 360px;
    height: 360px;
    display: grid;
    grid-row: 1;
    grid-column: 2;
    margin-left: 2rem;
    align-self: flex-end;
}

#resume,
#skills,
#projects,
#contact {
    text-align: center;
    font-family: "Oswald", sans-serif;
    display: flex;
    align-items: center;
    justify-content: center;
    background: radial-gradient(#CCE0D2 0%, #5e9b71 100%);
    border-bottom: 4px solid rgb(0, 120, 240);
    border-left: 2px solid rgb(0, 120, 240);
    border-right: 2px solid rgb(0, 120, 240);
    border-radius: 100%;
    box-shadow: 2px 2px 99px #61C2A2;
    width: 4.5rem;
    height: 4.5rem;
    transition: transform 1s;
}

#menu {
    display: grid;
    grid-row: span 2;
    justify-content: center;
    align-items: center;
}

#resume:hover,
#skills:hover,
#projects:hover,
#contact:hover {
    cursor: pointer;
    transform: scale(1.2);
}

