$numCols: 6;
$col-width: 100/$numCols;

.errands {
    display: grid;
    grid-template-columns: repeat(#{$numCols}, #{$col-width}vw);
    // grid-template-rows: repeat(4, minmax(30px, auto));
    grid-template-rows: 6rem 9rem 15rem 2rem;
    row-gap: 0.5rem;
    column-gap: 0.4rem;
    background: linear-gradient(rgba(0,0,0,0.8), rgba(0,0,0,0.1));
    height: 100vh;
    overflow: hidden;
    -ms-overflow-style: none;
    scrollbar-width: none;
}

.errands::-webkit-scrollbar {
    display: none;
}

.errands-title {
    grid-row: 1;
    grid-column: 1/ span 6;
    justify-self: center;
    text-shadow: 1px -2px 6px rgba(255,255,255,0.7);
    text-transform: uppercase;
    font-family: 'Kenia', cursive;
    font-size: 4.5rem;
    padding: 0;
    margin: 0;
    align-self: start;
}

.errand-category {
    text-transform: uppercase;
    font-family: 'Rubik Burned', cursive;
    cursor: ns-resize;
    grid-row: 2;
    font-size: 3rem;
    margin: 0;
    padding-bottom: 0.5rem;
}

#add-errand-button {
    background: linear-gradient(to bottom, #ed0404 60%, #fd7f1d);
    border-radius: 3px;
    font-weight: bold;
}

#add-errand-button:hover {
    cursor: pointer;
}

.errand-input {
    background: linear-gradient(20deg, rgba(255, 255, 255, 0.3), rgba(255, 255, 255, 0.1));
    border-bottom: 3px solid #ed0404;
    border-bottom-left-radius: 0.3rem;
    border-bottom-right-radius: 0.3rem;
    // padding: 0.5rem 3rem 0.1rem 0.4rem;
    padding-bottom: 0.1rem;
    padding-top: 0.4rem;
}

.errand-input::placeholder {
    color: black;
    font-family: 'Kenia', cursive;
    font-size: x-large;
}

input[type=date] {
    cursor:pointer;
}

.errand-input[type=text] {
    width: 8rem;
}

.inp {
    display: grid;
    grid-row: 2;
    align-self: start;
    justify-content: center;
    text-align: center;
    background: linear-gradient(to bottom, #ed0404 45%, #fd7f1d);
    border-top-right-radius: #{$col-width/2}vw;
    border-top-left-radius: #{$col-width/2}vw;
    box-shadow: -3px 3px 21px #ed0404;
    font-size: xx-large;
    padding: 0.9rem 0.1rem 0.6rem 0.1rem;
}

.inp, .outp {
    outline: 5px inset rgba(0, 111, 126, 0.7);
    font-family: 'Oleo Script Swash Caps', cursive;
    font-size: 1.5em;
    grid-column: 2/ span 4;
    align-items: center;
}

#errand-task {
    width: 9rem;
}

.outp {
    // display: grid;
    grid-row: 3;
    background: linear-gradient(to bottom, #fd7f1d 10%, #64fc45);
    border-bottom-right-radius: #{$col-width/4}vw;
    border-bottom-left-radius: #{$col-width/4}vw;
    box-shadow: 3px 3px 21px #64fc45;
    text-align: center;
    // display: flex;
    justify-content: space-evenly;
    overflow-y: scroll;
    overflow-x: scroll;
    -ms-overflow-style: none;
    scrollbar-width: none;
    padding: 0 1rem;
}

.outp::-webkit-scrollbar {
    display: none;
}